import React from 'react';
import { Table } from 'semantic-ui-react';
import { TOutputFormat } from '../utils/types/setupTypes';

export type TTableContent = {
	tableProps: {};
	header: [{ headerProps: any; headerRows: [{ headerRowProps: any; headerCells: [{ headerCellProps: any; headerCell: string }] }] }];
	body: [{ bodyProps: any; rows: [{ rowProps: any; cells: [{ cellProps: any; cell: string }] }] }];
};

type TProps = {
	row: TOutputFormat;
};

export const RenderLayoutTable = ({ row }: TProps) => {
	try {
		const tableContent: TTableContent = JSON.parse(row.content);

		return (
			<Table {...tableContent.tableProps} className="certificate-ui-table">
				{tableContent.header &&
					tableContent.header.map((headers) => (
						<Table.Header {...headers.headerProps}>
							{headers.headerRows.map((headerRow) => (
								<Table.Row {...headerRow.headerRowProps}>
									{headerRow.headerCells.map((cell) => (
										<Table.HeaderCell {...cell.headerCellProps}>{cell.headerCell}</Table.HeaderCell>
									))}
								</Table.Row>
							))}
						</Table.Header>
					))}
				{tableContent.body &&
					tableContent.body.map((bdy) => (
						<Table.Body {...bdy.bodyProps}>
							{bdy.rows.map((row) => (
								<Table.Row {...row.rowProps}>
									{row.cells.map((cell) => {
										const a = { 'text-align': 'left' };
										console.log(cell.cellProps.style, a, 'cell');

										return (
											<Table.Cell {...cell.cellProps} style={cell.cellProps.style}>
												{cell.cell}
											</Table.Cell>
										);
									})}
								</Table.Row>
							))}
						</Table.Body>
					))}
			</Table>
		);
	} catch {
		return null;
	}
};
